import { useState } from "react";

import Radio from "@material-ui/core/Radio";

function NaughtyChecked() {
  return <div>Naughty Checked</div>;
}

function NiceChecked() {
  return <div>Nice Checked</div>;
}

function VeryNiceChecked() {
  return <div>Very Nice Checked</div>;
}

function HowBehaved() {
  const [howBehaved, setHowBehaved] = useState("");
  return (
    <LetterToSantaSection>
      <Radio
        value="naughty"
        onChange={event => setHowBehaved(event.target.value)}
      />
      Naughty
      <Radio
        value="nice"
        onChange={event => setHowBehaved(event.target.value)}
        label="Nice"
      />{" "}
      Nice
      <Radio
        value="veryNice"
        onChange={event => setHowBehaved(event.target.value)}
      />{" "}
      Very Nice
      {howBehaved === "naughty" && <NaughtyChecked />}
      {howBehaved === "nice" && <NiceChecked />}
      {howBehaved === "veryNice" && <VeryNiceChecked />}
    </LetterToSantaSection>
  );
}

export default HowBehaved;
